* {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.product-card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  max-width: 200px;
  padding: 32px 16px 16px;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: flex;
}

.product-card__title {
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__description {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rating {
  justify-content: space-between;
  padding-top: 16px;
  display: flex;
}

.product-card__rating-reviews {
  color: #060b35;
  align-items: center;
  font-size: 10px;
  display: flex;
}

.product-card__price {
  justify-content: space-between;
  padding-top: 24px;
  padding-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.product-card__price-name {
  color: #616070;
  font-size: 12px;
}

.product-card__price-value {
  font-size: 16px;
  font-weight: bold;
}

.product-card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 39px;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 .stars__star:nth-child(-n+5) {
  background-image: url("star.822d561e.svg");
}

.stars--0 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

